      link: 'https://www.meetup.com/Peaceful-Healings/events/268810404/',
      description:
        '<p>Led by Marcia Hebrank, MSW and Jack Hebrank, Ph.D.</p> <p>Guest Faculty Members The Foundation for Shamanic Studies<br/>During this Basic experiential workshop, participants are introduced to Core Shamanism, the universal and near-universal basic methods used by shamans to enter non-ordinary reality for problem solving and healing. Particular emphasis is on the classic shamanic journey. The shamanic journey is one of the most remarkable visionary methods used by humankind to explore the hidden universe otherwise known mainly through myth and dream.</p> <p>Participants are initiated into shamanic journeying, aided by drumming and other techniques for experiencing the shamanic state of consciousness and for awakening dormant spiritual abilities, including connections with Nature. Practice includes comparisons by participants of their discoveries in shamanic journeys as well as being introduced to shamanic divination and healing. Participants are also provided with methods for journeying so they can meet and study with their own individual spirit helpers in non-ordinary reality, a classic step in shamanic practice.</p> <p>Participants learn how the journey is utilized to restore spiritual power and health, and how shamanism can be applied in contemporary daily life to heal oneself, others, and the Planet. Completion of this Basic Workshop with an authorized Foundation Faculty Member qualifies participants to take the advanced FSS workshops and courses. 13 FSS Continuing Education (CE) Credits are available for this workshop.<br/>Tuition: Early Bird - $240 received by 30 days before workshop; Regular - $270 $75 deposit required to hold a space (email for details)</p> <p>Online payment can be made to: Paypal.Me/MarciaHebrank</p> <p>To register or for further information, contact:</p> <p>Marcia or Jack Hebrank at (919)[masked] Email Contact: [masked] Also check out The Foundation for Shamanic Studies website at www.shamanism.org.</p> ',
